@mixin for-size ($size) {
    @if $size==phone {
        @media screen and (max-width: 767.98px) { @content; }
    } 
        @else if $size==tablet {
        @media screen and (min-width:768px) { @content;}
    }   
        
        @else if $size==desktop {
        @media screen and (min-width: 1280px) { @content; }
    }
}